@import "fonts";
* {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
	list-style: none;
	text-decoration: none;
}
textarea,
input {
	border: none;
	outline: none;
	font-size: 14px;
}
a,a:hover {
	text-decoration: none;
	outline: none;
	outline: 0;

}
img {
	border: none;
}
body, html {
	width: 100%;
	height: 100%;
}
body {
	background: url(../img/bg_body.jpg) repeat-y center;
	background-size: cover;
	color: #fff;
	font: 18px/1.15 'Data Control', sans-serif;
	font-weight: 300;
	position: relative;
	margin: 0 auto;
	min-width: 360px;
	display: table;
	max-width: 3500px;
}
#wrapper {
	max-width: 100%;
	margin: 0 auto;
	padding: 0 15px;
	display: table-cell;
	vertical-align: middle;
	.header {
		max-width: 98%;
		margin: 20px auto 15px;
		&:after {
			content: '';
			display: block;
			clear: both;
		}
		.logo {
			float: left;
			width: 250px;
			height: 44px;
			img {
				width: 100%;
			}
		}
		.mail {
			float: right;
			font-family: 'Tele-Marines', sans-serif;
			color: #fff;
			font-size: 27px;
			height: 44px;
			border: 1px solid #a56a54;
			background: rgba(0, 0, 0, 0.3);
			padding: 5px 20px;
		}
	}
	.main {
		max-width: 98%;
		border: 1px solid #a56a54;
		margin: 0 auto 15px;
		background:   rgba(0, 0, 0, 0.84);
		padding: 10px 10px 30px;	
		position: relative;
		overflow: hidden;
		// &:before {
		// 	content:'';
		// 	position: absolute;
		// 	width: 100%;
		// 	height: 100%;
		// 	right: 0;
		// 	left: 0;
		// 	bottom: 0;
		// 	background: url(../img/bg_main_line.png) repeat;
		// 	background-size: cover;
		// }
		.left_block {
			float: left;
			.top{
				margin: 0 0 25px;
				span {
					display: block;
					color: #fff;
				}
			}
			.bottom {
				display: block;
				color: #fff;
				margin: 0 0 30px;
				&:after {
					content: '';
					display: block;
					clear: both;
				}
				.coll {
					float: left;
					margin: 0 10px 0 0;
					text-align: right;
					&:last-child {
						margin: 0;
					}
					span {
						display: block;
					}
					span.head {
						padding: 0 0 7px;
						text-align: center;
					}
					span.head.left {
						text-align: left;
					}
					span.footer {
						padding: 7px 0 0;
					}
					.bottom_center {
						padding: 7px 0;
						position: relative;
						min-width: 75px;
						&:after {
							content: '';
							position: absolute;
							top: 0;
							left: 0;
							right: 0;
							width: 100%;
							height: 2px;
							background: url(../img/bg_line.png) repeat-x; 
						}
						&:before {
							content: '';
							position: absolute;
							bottom: 0;
							left: 0;
							right: 0;
							width: 100%;
							height: 2px;
							background: url(../img/bg_line.png) repeat-x; 
						}
					}
					.bottom_center.first {
						min-width: 135px;
					}
				}
				.coll.text__left {
					text-align: left;
				}
			}
			span.last {
				display: block;
			}
		}
		.center_block {
			float: left;
			width: 100%;
			margin: -130px 0 0;
			text-align: center;
			strong {
				font-weight: normal;
				font-size: 50px;
				display: block;
				margin: 0 0 30px;
				&:last-child {
					margin: 0;
				}
			}
			#barcode {
				margin: 0 auto;
				max-width: 250px;
				img{
					width: 250px;
				}
			}
			span.man {
				display: block;
				text-transform: uppercase;
				text-align: center;
				font-size: 24px;
				letter-spacing: 4px;
				margin: 0 -12px 30px 0;
			}
		}
	}
}
#footer {
	width: 100%;
	background: transparent;
	padding: 10px;
	span {
		display: block;
		text-align: right;
		font-size: 15px;
	}

}